<template>
	<div class="choice">
    <div class="warpper">
      <div :class="choiceClass1" @click="choiceClick1">境外目的地</div>
      <div :class="choiceClass2" @click="choiceClick2">境内目的地</div>
    </div>
	</div>
</template>

<script>
	export default {
		name: "CityChoice",
    props:{
      choice:Boolean
    },
    methods: {
      choiceClick1 () {
        this.$emit("choice",false);
      },
      choiceClick2 () {
      	this.$emit("choice",true);
      }
    },
    computed:{
      choiceClass1 () {
        if(this.choice){
          return "chiceBut"
        }else{
          return "chiceBut on"
        }
      },
      choiceClass2 () {
      	if(this.choice){
      		return "chiceBut on"
      	}else{
      		return "chiceBut"
      	}
      }
    }
	}
</script>

<style lang="stylus" scoped>
  @import "~styles/varibles.styl"
  .choice
    position:absolute
    top:headerHeight
    left:0
    right:0
    line-height:0.6rem
    text-align:center
    color:#fff
    background: bgColor
    padding-bottom: .1rem
    .warpper
      display:flex
      width:7.2rem    
      margin:0 auto
      margin-left 5%
      .chiceBut
        width:45%
      .on
        background:#FFF
        color:bgColor
        border-radius:.2rem
</style>