<template>
  <div class="icons">
    <swiper :options="swiperOption" v-if="iconList.length">
    	<!-- slides -->
    	<swiper-slide v-for="(page,index) of pages" :key="index">
    		<div class="icon" v-for="item of page" :key="item.id">
    				<img class="icon-img" :src="item.imgUrl" :alt="item.desc"> 
    				<div class="icon-desc">{{item.desc}}</div>
    		</div>
    	</swiper-slide>
    	<!-- Optional controls -->
    	<div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
  </div>
</template>
<script>
export default {
  name: "HomeIcons",
  props: {
    iconList: Array
  },
  data() {
    return {
      swiperOption:{
      	pagination: {
      		el: '.swiper-pagination',
      	}
      }
    }
  },
  computed: {
  	pages () {
      const pages = []
      this.iconList.forEach(function(item,index){
        const page = Math.floor(index / 8)
        if(!pages[page]){
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
  	}
  },
};
</script>
<style lang="stylus" scoped>
  .icons >>> .swiper-container
    width:100%
    height:0
    padding-bottom:56%
  .icons
    overflow:hidden
    width:100%
    height:0
    padding-top:5%
    padding-bottom:56%
    .icon
      position:relative
      overflow:hidden
      float:left
      width:25%
      padding-bottom:25%
      .icon-img
        position:absolute
        top:0
        right:0
        left:0
        width:100%
      .icon-desc
        position:absolute
        right:0
        bottom:0
        left:0
        line-height:.5rem
        height:.5rem
        color:#666666
        text-align:center
</style>