<template>
	<div class="gallary" @click="gallaryClick">
    <div class="wrappper">
      <swiper :options="swiperOptions">
      	<!-- slides -->
      	<swiper-slide v-for="(item, index) in images" :key="index">
      		<img class="gallary-images" :src="item">
      	</swiper-slide>
      	<!-- Optional controls -->
      	<div class="swiper-pagination"  slot="pagination"></div>
      </swiper>
    </div>
	</div>
</template>

<script>
	export default {
		name: "gallary",
    props: {
      images: {
        type: Array,
        default () {
          return ["https://img.alicdn.com/bao/uploaded/i4/2175412743/TB2um1TnhTI8KJjSspiXXbM4FXa_!!2175412743.jpg_640x640q30.jpg", "https://img.alicdn.com/bao/uploaded/i4/TB1GQgzeRfM8KJjSZFOH94r5XXa_074451.jpg_640x640q30.jpg"]
        }
      }
    },
    data () {
      return {
        swiperOptions: {
          observer:true,
          observeParents:true,
          loop : true,
          pagination: {
            el: '.swiper-pagination',
            type: 'fraction',
          },
        }
      }
    },
    methods: {
      gallaryClick () {
        this.$emit('close')
      }
    }
	}
</script>

<style lang="stylus" scoped>
.gallary >>> .swiper-container
  overflow: inherit
.gallary
  display: flex
  flex-direction: column
  justify-content: center
  z-index: 99
  position: fixed
  top: 0
  right: 0
  bottom: 0
  left: 0
  background: rgba(0,0,0,.9)
  .wrappper
    width: 100%
    height: 0
    padding-bottom: 100%
    background: #fff
    .gallary-images
      width:100%
    .swiper-pagination 
      color:#fff
      bottom: -1rem
</style>