<template>
	<div class="search">
    <router-link to="/">
    <div class="header-left iconfont">&#xe624;</div>
    </router-link>
    <input 
      class="header-center iconfont" 
      type="text" 
      value="" 
      placeholder="搜索地名请输汉字" 
      v-model="keyword"
    >
    <div v-show="keyword" class="search-content" ref="search">
      <div>
        <div class="search-item border-bottom" 
        v-for="item of list"
        :key="item.id"
        @click="cityClick(item.title)"
      >{{item.title}}</div>
      	<div class="search-item border-bottom" v-show="hasNoData" >没有找到匹配数据</div>
      </div>
    </div>
	</div>
</template>

<script>
  import Bscroll from 'better-scroll'
  import { mapMutations } from 'vuex'
  
	export default {
		name: "CitySearch",
    props:{
      domesticCity:Object,
      abroadCity:Object,
      choice:Boolean
    },
    data () {
      return {
        Citys: {},
        list: [],
        keyword: ''
      }
    },
    computed: {
      City () {
        if(this.choice){
          return this.domesticCity
        }else{
          return this.abroadCity
        }
      },
      hasNoData () {
        return !this.list.length
      }
   },
    watch: {
      keyword () {
        this.SearchItem(this.City)
      },
      choice () {
        if(this.choice){
        this.Citys = this.domesticCity
        }else{
        this.Citys = this.abroadCity
        }
        this.SearchItem(this.Citys)
      }
    },
    methods: {
    	SearchItem(Citys) {
    			const key = this.keyword
    			const result = []
    			for (let i in Citys) {
    				Citys[i].forEach(function (value) {
    					if (value.title.indexOf(key) > -1 && key != ""){
    						result.push(value)
    					}
    				})
    			}
    		this.list = result
    	},
      cityClick (city) {
      	this.changeCity(city)
      	this.$router.push('/')
      },
      ...mapMutations(['changeCity'])
    },
    mounted () {
    	this.scroll = new Bscroll(this.$refs.search,{click:true})
    }
	}
</script>

<style lang="stylus" scoped>
  @import "~styles/varibles.styl"
  .search
    position:absolute
    top:0
    width:100%
    line-height:headerHeight
    text-align:center
    .header-left
    	width:1rem
    	float:left
    	color:#fff
    .header-center
      float:left
      height:.6rem
      width:80%
      line-height:.6rem
      border-radius:.1rem
      background:#fff
      text-align:center
      color:#ccc
      padding:0 .1rem
      margin-top .1rem
    .search-content
      z-index:1
      position:absolute
      height:10000%
      top:1.7rem
      left:0
      right:0
      bottom:0
      background:#EEE
      .search-item
        line-height:.62rem
        background:#fff
        color:#666
</style>