<template>
    <div>
      <detail-bneer :images="images" :bannerInfo="bannerInfo"></detail-bneer>
      <detail-header></detail-header>
      <detail-list :list="list"></detail-list>
      <div class="cont"></div>
    </div>
</template>

<script>
  import DetailBneer from "@/components/detail/banner"
  import DetailHeader from "@/components/detail/header"
  import DetailList from "@/components/detail/list"
  import axios from "axios"
  
	export default {
		name: "Detail",
    components:{
      DetailBneer,
      DetailHeader,
      DetailList
    },
    data () {
      return {
        list: [],
        bannerInfo: "",
        images: [],
        lastId: ""
      }
    },
    methods: {
      getDetailInfo () {
        axios.get("/api/detail.json", {
          params: {
            id: this.$route.params.id
          }
        }).then(this.getDataSucc)
      },
      getDataSucc (res) {
        res = res.data
        if(res.ret && res.data) {
          const data = res.data
          this.list = data.list
          this.bannerInfo = data.bannerInfo
          this.images = data.images
        }
      }
    },
    mounted () {
      this.lastId = this.$route.params.id
      this.getDetailInfo ()
    },
    activated () {
    if (this.lastId === this.$route.params.id){}else{
      this.lastId = this.$route.params.id
      this.getDetailInfo()
    }
  }
}
</script>

<style lang="stylus" scoped>
.cont
  height 20rem
</style>