<template>
  <div class="home">
    <home-header></home-header>
    <home-swiper :swiperList="swiperList"></home-swiper>
    <home-icons :iconList="iconList"></home-icons>
    <home-random :randomList="randomList"></home-random>
    <home-like :likeList="likeList"></home-like>
  </div>
</template>
<script>
import HomeHeader from "@/components/Home/header"
import HomeSwiper from "@/components/Home/swiper"
import HomeIcons from "@/components/Home/icons"
import HomeRandom from "@/components/Home/random"
import HomeLike from "@/components/Home/like"
import axios from "axios"
import { mapState } from 'vuex'

export default {
  name: "Home",
  components:{
    HomeHeader,
    HomeSwiper,
    HomeIcons,
    HomeRandom,
    HomeLike
  },
  data() {
    return {
      swiperList: [],
      iconList: [],
      randomList: [],
      likeList: [],
      lastCity: ""
    }
  },
  methods: {
    getHomeInfo () {
      axios.get("/api/index.json?id=" + this.city)
            .then(this.getHomeInfdSucc)
    },
    getHomeInfdSucc (res){
      res = res.data
      if(res.ret && res.data){
        const data = res.data
        this.swiperList = data.swiperList
        this.iconList = data.iconList
        this.randomList = data.randomList
        this.likeList = data.likeList
      }
      
    }
  },
  computed: {
  	...mapState(['city'])
  },
  mounted () {
    this.lastCity = this.city
    this.getHomeInfo()
  },
  activated () {
    if (this.lastCity === this.city){}else{
      this.lastCity = this.city
      this.getHomeInfo()
    }
    
  }
};
</script>
