<template>
  <div>
  	<div class="banner" @click="bannerClick">
  		<swiper :options="swiperOptions">
      	<!-- slides -->
      	<swiper-slide v-for="(item, index) in images" :key="index">
      		<img class="banner-images" :src="item">
      	</swiper-slide>
      	<!-- Optional controls -->
      	<div class="swiper-pagination"  slot="pagination"></div>
      </swiper>
  		<div class="banner-info">{{this.bannerInfo}}</div>
  	</div>
    <fade-animation>
      <gallary 
      :images="images" 
      v-show="showGallary" 
      @close="gallaryClose"></gallary>
    </fade-animation>
  </div>
	
</template>

<script>
  import gallary from '@/components/common/gallary/gallary'
  import FadeAnimation from '@/components/common/fade/fade'
	export default {
    name: "DetailBneer",
    props: {
      images: Array,
      bannerInfo: String
    },
    data () {
      return {
        showGallary: false,
        swiperOptions: {
          observer:true,
          observeParents:true,
          delay:3000,
          autoplay: true,
          loop : true,
          pagination: {
            el: '.swiper-pagination',
          },
        },
        
      }
    },
    components:{
      gallary,
      FadeAnimation
    },
    methods: {
      bannerClick () {
        this.showGallary = true
      },
      gallaryClose () {
        this.showGallary = false
      }
    }
	}
</script>

<style lang="stylus" scoped>
  .banner >>> .swiper-pagination-bullet-active
    background #ffffff
  .banner 
    overflow:hidden
    position: relative
    width:100%
    height:0
    padding-bottom:100%
    .banner-images
      width:100%
    .banner-info
      z-index 2
      overflow: hidden
      position: absolute
      left: .4rem
      bottom: .3rem
      max-width: 3.6rem
      line-height:.8rem
      border-radius: 12px
      font-weight:bold
      color:#fff
      white-space: nowrap
      text-overflow: ellipsis
      padding:0 0.4rem;
      background-image: linear-gradient(top, rgba(0,0,0,0), rgba(0,0,0,.8))
</style>