<template>
	<ul class="list">
    <li class="item" 
      v-for="(item,key) of City" 
      :key="key"
      @click="handleClick"
      @touchstart.prevent="handleTouchStart"
      @touchmove="handleTouchMove"
      @touchend="handleTouchEnd"
      :ref="item"
    >{{key}}</li>
	</ul>
</template>

<script>
	export default {
		name: "CityAlphabet",
    props:{
      choice: Boolean,
      domesticCity:Object,
      abroadCity:Object,
    },
    data () {
      return {
        touchStatus: false,
        
      }
    },
    methods: {
      handleClick (e) {
        this.$emit("change",e.target.innerText)
      },
      handleTouchStart () {
        this.touchStatus = true
      },
      handleTouchMove (e) {
        /* if (this.touchStatus) {
          const startY = this.$refs['A'] [0].offsetTop
          const touchY = e.touches[0].clientY
          const index = Math.floor((touchY - startY) / 20)
          if( index >= 0 && index < this.City.length){
            this.$emit("change", this.City[index])
          }
        } */
      },
      handleTouchEnd () {
        this.touchStatus = false
      }
    },
    computed: {
    		City () {
    			if(this.choice){
    				return this.domesticCity
    			}else{
    				return this.abroadCity
    			}
    		}
    }
	}
</script>

<style lang="stylus" scoped>
  @import "~styles/varibles.styl"
  .list
    display:flex
    flex-direction: column
    justify-content: center
    width:.4rem
    margin-top:1.7rem
    margin-left:94%
    .item
      overflow:hidden
      text-align:center
      color:bgColor
      line-height: .35rem
      z-index:1
</style>