<template>
	<div class="header">
    <router-link 
      v-show="showBack"
      to="/" 
      tag="div" 
      class="header-back iconfont"
     >&#xe624;</router-link>
    <div 
      class="header-fixed" 
      v-show="!showBack"
      :style="opacityStyle"
     >
      <router-link to="/" tag="div" class="header-fixed-back iconfont">&#xe624;</router-link>
      <div class="header-title">景点详情</div>
    </div>
	</div>
</template>

<script>
	export default {
		name: "DetailHeader",
    data () {
      return {
        showBack: true,
        opacityStyle: {
          opacity: 0
        }
      }
    },
    methods:{
      headerScroll () {
        let top = document.documentElement.scrollTop
        if(top > 60){
          let opacity = top / 140
          if(opacity > 1){
            opacity = 1
          }
          this.opacityStyle = {
            opacity
          }
          this.showBack=false
        }else{
          this.showBack=true
        }
      }
    },
    activated () {
      window.addEventListener('scroll', this.headerScroll)
    },
    deactivated () {
      window.removeEventListener('scroll', this.headerScroll)
    }
	}
</script>

<style lang="stylus" scoped>
  @import "~styles/varibles.styl"
  .header
    z-index: 2
    position:fixed
    top:0
    left:0
    right:0
    color: #fff
    .header-back
      position: absolute
      top: .1rem
      left: .1rem
      width: headerHeight
      height: headerHeight
      border-radius: 50%
      background: rgba(0,0,0,.5)
      text-align: center
      line-height: headerHeight
    .header-fixed
      display:flex
      line-height:headerHeight
      text-align:center
      background:bgColor
      .header-fixed-back
        width:1rem
      .header-title
        flex:1
        
</style>