<template>
	<div class="list" ref="wrapper">
    <div>
      <div class="area">
        <div class="title border-topbottom">当前城市</div>
        <div class="button-list">
          <div class="button-wrapper">
            <div class="button">{{this.currentCity}}</div>
          </div>
        </div>
      </div>
      <div class="area">
        <div class="title border-topbottom">热门城市</div>
        <div class="button-list">
          <div class="button-wrapper"  
            v-if="Recommend.length" 
            v-for="item of Recommend" 
            :key="item.id" 
            @click="cityClick(item.title)"
            >
            <div class="button">{{item.title}}</div>
          </div>
        </div>
      </div>
      <div class="area" 
        v-if="City" 
        v-for="(items,key) of City" 
        :key="key"
      >
        <div class="title border-topbottom" :ref="key">{{key}}</div>
        <div class="item-list">
          <div 
            class="item border-bottom" 
            v-for="item of items" 
            :key="item.id"
            @click="cityClick(item.title)"

          >{{item.title}}</div>
        </div>
      </div>
    </div>
	</div>
</template>

<script>
  import Bscroll from 'better-scroll'
  import { mapState,mapMutations } from 'vuex'
  
	export default {
		name: "CityList",
    props:{
      choice: Boolean,
      domesticCity:Object,
      domesticRecommend:Array,
      abroadCity:Object,
      abroadRecommend:Array,
      letter:String
    },
    computed: {
      
        City () {
          if(this.choice){
            return this.domesticCity
          }else{
            return this.abroadCity
          }
        },
        Recommend () {
          if(this.choice){
          	return this.domesticRecommend
          }else{
          	return this.abroadRecommend
          }
        },
        ...mapState({
          currentCity: 'city'
        })
      
    },
    watch: {
      letter () {
        if (this.letter){
          const element = this.$refs[this.letter] [0]
          this.scroll.scrollToElement(element)
        }
      }
    },
    methods:{
      cityClick (city) {
        this.changeCity(city)
        this.$router.push('/')
      },
      ...mapMutations(['changeCity'])
    },
    mounted () {
      this.scroll = new Bscroll(this.$refs.wrapper,{click:true})
    }
	}
</script>

<style lang="stylus" scoped>
  .border-topbottom
    &:before
      border-color:#ccc
    &:after
      border-color:#ccc
  .border-bottom
    &:before
      border-color:#ccc
  .list
    overflow:hidden
    position:absolute
    top:1.7rem
    left:0
    right:0
    bottom:0
    
    .title
      line-height:.54rem
      background:#eee
      padding-left:.2rem
      color:#666666
      font-size:.26rem
    .button-list
      overflow:hidden
      padding:.1rem .6rem .1rem .1rem
      .button-wrapper
        float:left
        width:33.33%
        .button
          margin:.1rem
          padding: .1rem 0
          text-align:center
          border:.02rem solid #ccc
          border-radius:.06rem
    .item-list
      .item
        line-height:.76rem
        padding-left:.2rem
</style>