<template>
	<div class="city">
    <city-header></city-header>
    <city-search 
      :domesticCity="domesticCity" 
      :abroadCity="abroadCity" 
      :choice="choice"
    ></city-search>
    <city-choice
      :choice="choice"
      @choice="choiceClick"
    ></city-choice>
    <city-list 
      :domesticCity="domesticCity" 
      :domesticRecommend="domesticRecommend" 
      :abroadCity="abroadCity" 
      :abroadRecommend= "abroadRecommend" 
      :choice="choice"
      :letter="letter"
    ></city-list>
    <city-alphabet
      @change="letterChange"
      :domesticCity="domesticCity" 
      :abroadCity="abroadCity" 
      :choice="choice"
    ></city-alphabet>
	</div>
</template>

<script>
  import axios from 'axios'
  import CityHeader from "@/components/City/header"
  import CityChoice from "@/components/City/choice"
  import CityList from "@/components/City/list"
  import CityAlphabet from "@/components/City/alphabet"
 import CitySearch from "@/components/City/search"
  
	export default {
		name: "City",
    data () {
      return {
        domesticCity:{},
        domesticRecommend:[],
        abroadCity:{},
        abroadRecommend:[],
        choice:true,
        letter: " "
      }
    },
    components: {
      CityHeader,
      CityChoice,
      CityList,
      CityAlphabet,
      CitySearch
    },
    methods: {
      letterChange (letter) {
        this.letter = letter
      },
      choiceClick (x) {
        this.choice=x
      },
      getCityInfo () {
        axios.get('/api/city.json')
             .then(this.grtCityInfoSucc)
      },
      grtCityInfoSucc (res) {
        res = res.data
        if (res.ret && res.data){
          const data = res.data
          this.ret = true
          this.domesticCity = data.domestic.city
          this.domesticRecommend = data.domestic.recommend.list
          this.abroadCity = data.abroad.city
          this.abroadRecommend = data.abroad.recommend.list
          
        }
      }
    },
    mounted () {
      this.getCityInfo()
      
    }
    
	}
</script>

<style lang="stylus" scoped>
  
</style>